* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

h2, p {
  margin: 0;
  padding: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
}

.card-top {
  justify-content: center;
  align-items: center;
  padding: 32px 19px 36px;
  display: flex;
}

.card-product {
  flex-direction: column;
  gap: 4px;
  padding-inline: 16px;
  display: flex;
}

.card-product__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card-product__code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card-count {
  justify-content: space-between;
  margin-top: 24px;
  padding-inline: 16px;
  display: flex;
}

.card-count__price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card-count__value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card-button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  padding-inline: 16px;
  display: flex;
}

.stars-quality {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.stars-quality__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars-quality--4 li:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars-quality__star:not(li:last-child) {
  margin-right: 4px;
}

.stars__reviews {
  color: #060b35;
  padding-top: 2px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

